.confirm_modal {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.confirm_modal_content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, .5);
}
.confirm_modal p {
  margin: 0 0 10px 0
}

.confirm_modal_buttons {
  margin-right: 10px;
}
